@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&family=Comfortaa:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,h6{font-weight:700}body,h1,h2,h3,h4,h5,h6,p,span,input,button{font-family:"Comfortaa", cursive}.novelWrapper{position:relative;height:100vh;display:flex;flex-direction:column;align-items:stretch;justify-content:end;overflow:hidden}.novelWrapper .bcg{position:absolute;top:0;left:0;z-index:0;width:100vw;height:100vh;object-fit:cover}.novelWrapper .characters{position:absolute;left:0;right:0;top:0;bottom:0;z-index:1;display:flex;justify-content:center;flex-direction:column}.novelWrapper .characters .character{position:absolute;height:90vh}.novelWrapper .characters .left{align-self:start;margin-left:4rem}.novelWrapper .characters .right{align-self:end;margin-right:4rem}.novelWrapper .characters .center{align-self:center}

.dialog{display:flex;flex-direction:column;height:30vh;z-index:2;background-color:rgba(51,51,51,0.55);margin:1rem;padding:2rem;align-items:flex-start;border-radius:2rem;overflow-y:scroll;scrollbar-width:none;-ms-overflow-style:none;scroll-behavior:smooth}.dialog .dialogEntry{border-radius:2rem;border-top-right-radius:0;max-width:70%;height:-webkit-min-content;height:min-content;position:relative;margin-bottom:1rem}.dialog .dialogEntry .character_name{width:-webkit-min-content;width:min-content;margin:0;padding:2rem;padding-top:1.5rem;padding-bottom:0;background-color:#444;border-radius:2rem;border-bottom-left-radius:0;border-bottom-right-radius:0;position:relative}.dialog .dialogEntry .character_name::after{display:block;left:0;right:0;top:2rem;bottom:-2rem;content:"";position:absolute;background-color:#444;z-index:-1}.dialog .dialogEntry .text{background-color:#444;padding:1em 2rem;border-radius:2rem;border-top-left-radius:0}.dialog .dialogEntry p{color:#fff}.dialog .dialogEntry.Waifu{align-self:flex-end}.dialog .dialogEntry.Waifu .text{background-color:#444}.dialog .dialogEntry.Waifu .character_name{background-color:#444}.dialog .dialogEntry.Waifu .character_name::after{background-color:#444}

.diaInput{background-color:rgba(51,51,51,0.55);z-index:1;padding:0.25rem;margin:1rem;margin-top:0;border-radius:2rem;display:flex}.diaInput input{background-color:transparent;border:none;flex-grow:1;font-size:1.25rem;color:white;margin-left:2rem}.diaInput input:focus-visible{border:none;outline:none}.diaInput button{aspect-ratio:1/1;padding:0.5em;border-radius:1rem;font-size:2rem;line-height:1rem;background-color:white;border:0;margin:0.5rem;margin-left:2rem}

